@import "variables";

.button {
  display: inline-flex;
  position: relative;
  text-decoration: none;
  .buttonStyle();
  &::after {
    .buttonIeActiveFix;
  }
  &:hover:not(:disabled) {
    background: @mediumGray;
  }
  &:focus {
    outline: none;
    &::before {
      .buttonFocusBefore();
    }
  }
  &:active:not(:disabled) {
    top: 1px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
  }
  &__inner {
    position: relative;
    display: flex;
    align-items: center;
  }
  &__text {
    margin: 0 10px;
  }
  &:disabled {
    opacity: 0.5;
    cursor: default;
  }
}
